import {createRouter, createWebHashHistory} from 'vue-router'
import WordTwo from "@/view/WordTwo/WordTwo";

const routes = [
    {
        path: '/',
        name: 'Home',
        component: () => import('@/view/CloudMusic/CloudMusic')
    },
    {
        path: '/hello',
        name: 'WordOne',
        component: () => import('@/view/WordOne/WordOne')
    },
    {
        path: '/wrold',
        name: 'WordTwo',
        component: WordTwo
    },
    {
        path: '/t',
        name: 'WordThree',
        component: () => import('@/view/WordThree/WordThree')
    },
    {
        path: '/echarts',
        name: 'MyEcharts',
        component: () => import('@/view/MyEchart/MyEcharts')
    },
    {
        path: '/echart',
        name: 'EchartsIndex',
        component: () => import('@/view/EchartHtml/EchartIndex')
    },
    {
        path: '/test',
        name: 'MyTest',
        component: () => import('@/view/EleMent/MyTest'),
        meta: {
            name: 18
        }
    }
]

const router = createRouter({
    history: createWebHashHistory(),
    routes
})

// router.beforeEach((to, from) => console.log(to, from))
export default router
